import React, { useState, useEffect } from 'react';
import './App.css';
import MaterialTable from 'material-table'
var data1 = require('./data.json')


function App() {


  const columns = [
    { title: "year", field: "year"},
    { title: "category", field: "category" },
    { title: "Name", field: "name" }
  ]

  return (
    <div className="App">
      <MaterialTable
        title="Winners Data"
        data={data1.prizes}
        columns={columns}
        options={{
            sorting: true
          }}
      />
    </div>
  );
}

export default App;